<template>
    <div class="snb">
        <div class="inner">
            <div class="user-menu">
                <div class="alert">
                    <a class="" href=""></a>
                </div>
                <button @click="toggle = !toggle" class="info" v-click-outside="closeDom">
                    <div class="user">
                        <span>{{name}}</span>
                        <span>님</span>
                    </div>
                    <ul v-if="toggle" class="info-list">
                        <li class="id">
                            <span>ID :</span>
                            <span>{{id}}</span>
                        </li>
                        <li>
                            <a>내 정보</a>
                        </li>
                        <li @click="doLogout">
                            <a @click="doLogout">로그아웃</a>
                        </li>
                    </ul>
                </button>
            </div>
            <div class="select-box">
                <select class="gray" name="" id="">
                    <option value="">한국기준(UTF-9)</option>
                    <option value="">사용국가기준(UTF-?)</option>
                    <option value="">서버기준(UTF-0)</option>
                </select>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Header',
    data() {
        return {
            toggle: false,
            name: localStorage.getItem('name'),
            id: localStorage.getItem('id'),
        }
    },
    methods: {
        doLogout() {    // 로그아웃
            this.$store.dispatch('doLogout').then(() => {})
        },
        closeDom() {    // dom 외부 클릭
            if(this.toggle) {
                this.toggle = false
            }
        }
    },
}
</script>

